import { NextResponse } from 'next/server';
import { db } from '@/lib/firebase-admin';
import { Supply } from '@/types/schema';

export const dynamic = 'force-dynamic'; // Ensure no caching for real-time data

export async function GET() {
    try {
        const snapshot = await db.collection('supplies').orderBy('createdAt', 'desc').get();

        const supplies: Supply[] = [];
        snapshot.forEach(doc => {
            supplies.push(doc.data() as Supply);
        });

        return NextResponse.json(supplies);
    } catch (error: any) {
        console.error("Supplies GET API Error:", error);
        return NextResponse.json({ error: error.message || "Internal Server Error" }, { status: 500 });
    }
}
